import React, { Component } from 'react';
import Button from '../../UI/Button/Button'
import Auxiliary from '../../../hoc/Auxiliary/Auxiliary'

class OrderSummary extends Component {

    // shouldComponentUpdate() {
    //     console.log('[orderSummary] shouldUpdate')
    // }

    render() {
        const ingredientSummary = Object.keys(this.props.ingredients)
            .map(igKey => {
                return (
                    <li key={igKey} >
                        <span
                            style={{ textTransform: 'capitalize' }}>{igKey}
                        </span>
                 : {this.props.ingredients[igKey]}
                    </li>
                )
            })

        return (
            <Auxiliary>
                <h3> Your Order</h3>
                <p>A delicious burger with the following ingredients:</p>
                <ul>
                    {ingredientSummary}
                </ul>
                <p><strong>Total Price : {this.props.price.toFixed(2)}</strong></p>
                <p>Continue to Checkout?</p>
                <Button btnType='Danger' clicked={this.props.purchasedCanceled} >CANCEL</Button>
                <Button btnType="Success" clicked={this.props.purchasedContinued}>CONTINUE</Button>
            </Auxiliary>
        );
    }
}

export default OrderSummary;


